import * as Flux from 'flux';

import IAction from './../Actions/IAction';

export default class AppDispatcher extends Flux.Dispatcher<{}> {
  private static _instance: AppDispatcher;

  public static get instance(): AppDispatcher {
    if (!this._instance) {
      this._instance = new AppDispatcher();
    }

    return this._instance;
  }

  public dispatch(payload: IAction): void {
    try {
      super.dispatch(payload);
    } catch (error) {
      /* @todo: log. VSO#206341 */

      throw error;
    }
  }
}
